.form-contacto{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    border: 1px solid $negro;
    padding: 1rem;
    width: 80rem;
    margin-bottom: 2rem;

    .campo{
        border-radius: 1rem;
        border: 1px solid $azul;
        width: 60rem;
        padding: 2rem;
        margin-top: 0;
    }
}

.botones-form{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.cursor-not-allowed{
    cursor: not-allowed;
}
.opacity-50 {
    opacity: .5;
}

.error{
    color: red;
    border: 1px solid red;
    text-align: center;
}

.border-red-500{
    border:2px solid red !important;
}
.border-green-500{
    border:2px solid green !important;
}