.header {
  background-image: url(../img/hero.jpg);
  height: 70rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 2rem;
}

.logo {
  order: 1;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  .logo_texto {
    color: $blanco;
    h1 {
      font-weight: $bold;
    }
  }
  .logo_imagen {
    img {
      width: 10rem;
      height: 10rem;
    }
  }
}

.navegacion {
  order: 3;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  li{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    ul{
      padding-left: 0;
      text-align: center;
      position: absolute;
      top: 60%;
      background-color: $negro;
      display: none;
    }
    &:hover ul{
      z-index: 2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
    }
  }
  .nav_enlace {
    color: $blanco;
    font-size: 2rem;
  }
}


//Carrito
.carrito-compras {
  order: 2;
  &:hover .contenido {
    z-index: 2;
    display: block;
    position: absolute;
    right: 0;
    background-color: #e1e1e1;
    padding: 1rem;
    max-width: 380px;
  }
}

.lista {
  th,
  td {
    padding: 6px 8px;
    padding-left: 8px;
    text-align: center;
    border-bottom: 1px solid #e1e1e1;
  }
}
.oculto {
  display: none;
}

@include tablet{
    .carrito-compras{
        position: relative;
        &:hover .contenido{
          max-width: 580px;
        }
    }

    .lista {
      th,td {
        padding: 12px 15px;
        padding-left: 15px;
      }
    }
}

.borrar-curso {
  background-color: red;
  border-radius: 50%;
  padding: 3px 8px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.lista{
  width: 80%;
}
//
@include tablet {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
  .navegacion {
    order: 2;
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
  }
  .logo {
    order: 1;
  }
  .carrito-compras {
    order: 3;
  }
}

.contenido-hero {
  color: $blanco;
  font-weight: $bold;
  font-size: 5rem;

  form {
    display: flex;
    justify-content: center;
    height: 4rem;
    gap: 1rem;
    margin-top: 2rem;
    .busqueda {
      font-size: 1.6rem;
    }
    input[type="submit"] {
      background: $azul;
      font-size: 1.6rem;
      color: $blanco;
      padding: 1rem;
    }
  }
}

@include tablet {
  .contenido-hero {
    form {
      height: auto;
      .busqueda {
        margin-top: 4rem;
        font-size: 3rem;
      }
      input[type="submit"] {
        margin-top: 4rem;
        font-size: 3rem;
      }
    }
  }
}
