body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 80rem; }

.alinear-derecha {
  display: flex;
  justify-content: flex-end; }

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  display: inline-block;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem; }

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  display: block;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem; }

.boton-azul {
  background-color: #3b3bd4;
  color: #FFFFFF;
  display: inline-block;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem; }

.boton-azul-block {
  background-color: #3b3bd4;
  color: #FFFFFF;
  display: block;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  color: #FFFFFF !important; }

.dark-mode {
  background-color: #1a1a1a; }
  .dark-mode p {
    color: #FFFFFF; }
  .dark-mode h1,
  .dark-mode h2,
  .dark-mode h3,
  .dark-mode h4 {
    color: #e1e1e1; }
  .dark-mode .anuncio,
  .dark-mode .resumen-propiedad {
    background-color: #333333;
    border: 1px solid #333333; }
    .dark-mode .anuncio .precio,
    .dark-mode .resumen-propiedad .precio {
      color: #71B100; }
    .dark-mode .anuncio .icono,
    .dark-mode .resumen-propiedad .icono {
      filter: invert(100%); }
  .dark-mode .texto-nosotros blockquote {
    color: #FFFFFF; }
  .dark-mode .formulario label,
  .dark-mode .formulario legend,
  .dark-mode .formulario p {
    color: #e1e1e1; }
  .dark-mode .formulario input:not([type="submit"]),
  .dark-mode .formulario textarea,
  .dark-mode .formulario select {
    background-color: #333333;
    border: 1px solid #333333;
    color: #FFFFFF; }

.header {
  background-image: url(../img/hero.jpg);
  height: 70rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center; }

.barra {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 2rem; }

.logo {
  order: 1;
  display: flex;
  justify-content: space-between;
  gap: 2rem; }
  .logo .logo_texto {
    color: #FFFFFF; }
    .logo .logo_texto h1 {
      font-weight: 700; }
  .logo .logo_imagen img {
    width: 10rem;
    height: 10rem; }

.navegacion {
  order: 3;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .navegacion li {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem; }
    .navegacion li ul {
      padding-left: 0;
      text-align: center;
      position: absolute;
      top: 60%;
      background-color: #000000;
      display: none; }
    .navegacion li:hover ul {
      z-index: 2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%; }
  .navegacion .nav_enlace {
    color: #FFFFFF;
    font-size: 2rem; }

.carrito-compras {
  order: 2; }
  .carrito-compras:hover .contenido {
    z-index: 2;
    display: block;
    position: absolute;
    right: 0;
    background-color: #e1e1e1;
    padding: 1rem;
    max-width: 380px; }

.lista th,
.lista td {
  padding: 6px 8px;
  padding-left: 8px;
  text-align: center;
  border-bottom: 1px solid #e1e1e1; }

.oculto {
  display: none; }

@media (min-width: 768px) {
  .carrito-compras {
    position: relative; }
    .carrito-compras:hover .contenido {
      max-width: 580px; }
  .lista th, .lista td {
    padding: 12px 15px;
    padding-left: 15px; } }

.borrar-curso {
  background-color: red;
  border-radius: 50%;
  padding: 3px 8px;
  text-decoration: none;
  color: white;
  font-weight: bold; }

.lista {
  width: 80%; }

@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between; }
  .navegacion {
    order: 2;
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem; }
  .logo {
    order: 1; }
  .carrito-compras {
    order: 3; } }

.contenido-hero {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 5rem; }
  .contenido-hero form {
    display: flex;
    justify-content: center;
    height: 4rem;
    gap: 1rem;
    margin-top: 2rem; }
    .contenido-hero form .busqueda {
      font-size: 1.6rem; }
    .contenido-hero form input[type="submit"] {
      background: #3b3bd4;
      font-size: 1.6rem;
      color: #FFFFFF;
      padding: 1rem; }

@media (min-width: 768px) {
  .contenido-hero form {
    height: auto; }
    .contenido-hero form .busqueda {
      margin-top: 4rem;
      font-size: 3rem; }
    .contenido-hero form input[type="submit"] {
      margin-top: 4rem;
      font-size: 3rem; } }

.articulos {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

@media (min-width: 768px) {
  .articulos {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 2rem; } }

.articulo {
  flex: 0 1 100%;
  border: 1px solid #e1e1e1;
  margin-bottom: 1rem; }
  .articulo .estrellas {
    width: 30%; }
  .articulo .imagen-articulo {
    width: 100%;
    height: 40rem; }
  .articulo .precio {
    display: flex;
    justify-content: space-between; }
    .articulo .precio .regular {
      text-decoration: line-through;
      color: #E08709; }
    .articulo .precio .oferta {
      font-size: 2.6rem;
      font-weight: 700;
      color: #71B100; }

.info-articulo {
  padding: 1rem; }
  .info-articulo h4 {
    text-align: center;
    margin: 0;
    font-size: 2.4rem;
    font-weight: 900; }
  .info-articulo a {
    color: #333333;
    font-size: 1.5rem; }
    .info-articulo a:hover {
      color: #71B100 !important; }

@media (min-width: 768px) {
  .info-articulo a {
    font-size: 1.7rem; } }

.footer {
  background-color: #000000;
  padding-bottom: 2rem; }
  .footer h1 {
    margin: 0;
    color: #FFFFFF;
    font-size: 2rem; }

.form-contacto {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  border: 1px solid #000000;
  padding: 1rem;
  width: 80rem;
  margin-bottom: 2rem; }
  .form-contacto .campo {
    border-radius: 1rem;
    border: 1px solid #3b3bd4;
    width: 60rem;
    padding: 2rem;
    margin-top: 0; }

.botones-form {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem; }

.cursor-not-allowed {
  cursor: not-allowed; }

.opacity-50 {
  opacity: .5; }

.error {
  color: red;
  border: 1px solid red;
  text-align: center; }

.border-red-500 {
  border: 2px solid red !important; }

.border-green-500 {
  border: 2px solid green !important; }
