.articulos {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@include tablet{
  .articulos{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    column-gap: 2rem;
  }
}
.articulo {
  flex: 0 1 100%;
  border: 1px solid $gris;
  margin-bottom: 1rem;
  .estrellas {
    width: 30%;
  }
  .imagen-articulo {
    width: 100%;
    height: 40rem;
  }
  .precio {
    display: flex;
    justify-content: space-between;

    .regular {
      text-decoration: line-through;
      color: $amarillo;
    }

    .oferta {
      font-size: 2.6rem;
      font-weight: $bold;
      color: $verde;
    }
  }
}

.info-articulo {
  padding: 1rem;
  h4 {
    text-align: center;
    margin: 0;
    font-size: 2.4rem;
    font-weight: $black;
  }
  a{
    color: $grisOscuro;
    font-size: 1.5rem;
    &:hover{
      color: $verde !important;
    }
  }
}

@include tablet{
  .info-articulo{
    a{
      font-size: 1.7rem;
    }
  }
}